import axios from "axios";
import { useEffect, useState } from "react"
import { Link } from "react-router-dom";


export function DesignDeadCharector(){
    const [deads , setDead] = useState([]);

    useEffect(()=>{
        axios({
            method: 'get',
            url : 'https://rickandmortyapi.com/api/character/?name=rick&status=dead'
        })
        .then(response=>{
            setDead(response.data.results);
        })
    },[]);
    return(
        <div className="container-fluid">
            <header className="d-flex justify-content-between p-1">
                <h2>Dead Characters</h2>
                <Link className="btn btn-danger h-25 me-4" to='/'>Back</Link>
            </header>
            <div className="d-flex flex-wrap">
                {
                    deads.map(dead=>
                        <div key={dead.id} className="card p-1 m-2" style={{width:'230px'}}>
                            <img src={dead.image} height='190px' className="card-img-top" />
                            <div className="card-header" style={{height:'140px'}}>
                                <dl>
                                    <dt>Name</dt>
                                    <dd>{dead.name}</dd>
                                    <dt>Species</dt>
                                    <dd>{dead.species}</dd>
                                </dl>
                            </div>
                            <div className="card-footer">
                                <Link to={'/deadchar/details/' + dead.id} className="btn btn-primary mt-2 w-100 p-1 m-1">Details</Link>
                            </div>
                        </div>
                    )
                }
            </div>
        </div>
    )
}