import axios from "axios";
import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom"
import './designdetail.css';


export function DesignAliveDetail(){

    const params = useParams();
    const [products , setProducts] = useState({id:0,name:'',status:'',species:'',gender:''});
    
    useEffect(()=>{
        axios({
            method:'get',
            url:`https://rickandmortyapi.com/api/character/${params.id}`    
        })
        .then(response=>{
            setProducts(response.data);
        })
    },[]);
    return(
        <div className="container-fluid">
            <h2 className="mt-4">{products.name} Details</h2>
            <div className="row">
                <div className="col-4 mt-5">
                    <img src={products.image} height='200' width='200' />
                </div>
                <div className="col-8">
                    <dl>
                       <dt>Id</dt>
                       <dd>{products.id}</dd>
                       <dt>Name</dt>
                       <dd>{products.name}</dd>
                       <dt>Status</dt>
                       <dd>{products.status}</dd>
                       <dt>Species</dt>
                       <dd>{products.species}</dd>
                       <dt>Gender</dt>
                       <dd>{products.gender}</dd>
                    </dl>
                    <div>
                    <Link className="btn btn-primary w-25 p-1" to='/alivechar'>Back to Alive Characters</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}