import { BrowserRouter, Route, Routes } from "react-router-dom";
import { DesignHome } from "../design-index/home";
import { DesignAllCharector } from "../design-char/char";
import { DesignAliveCharector } from "../design-char/alivechar";
import { DesignDeadCharector } from "../design-char/deadchar";
import { DesignDetail } from "../design-detail/details";
import { DesignAliveDetail } from "../design-detail/designalive";
import { DesignDeadDetails } from "../design-detail/designdead";



export function DesignIndex(){

    return(
        <div className="container-fluid">
            <BrowserRouter>
                <div className="mt-2 bg-dark text-white text-center p-3 mt-3">
                    <strong>⚡️ THE STORY OF DEALS RICK AND MORTY ⚡️</strong>
                </div>

                <Routes>
                    <Route path='/' element={<DesignHome />}></Route>
                    <Route path="allchar" element={<DesignAllCharector />}></Route>
                    <Route path="alivechar" element={<DesignAliveCharector />}></Route>
                    <Route path="deadchar" element={<DesignDeadCharector />}></Route>
                    <Route path="allchar/details/:id" element={<DesignDetail />}></Route>
                    <Route path="alivechar/details/:id" element={<DesignAliveDetail />}></Route>
                    <Route path="deadchar/details/:id" element={<DesignDeadDetails />}></Route>
                </Routes>
            </BrowserRouter>
        </div>
    )
}