
import { Link } from 'react-router-dom';
import './home.css';


export function DesignHome(){

    return(
        <div className="container-fluid">
                {/* <header className="text-center mt-4">
                    <h2 >The story of Rick and Morty</h2>
                </header> */}
            <div className='main-card'>
                <div className="card1">
                    <h2>01</h2>
                    <h3>All Characters</h3>
                    <p>Please click the button and get all charecters.</p>
                    <Link to='/allchar' id='allBtn'>All Characters</Link>
                </div>
                <div className="card1">
                    <h2>02</h2>
                    <h3>Alive Characters</h3>
                    <p>Please click the button and get only alive charecters.</p>
                    <Link to='/alivechar' id='allBtn'>Alive Characters</Link>
                </div>
                <div className="card1">
                    <h2>03</h2>
                    <h3>Dead Characters</h3>
                    <p>Please click the button and get only dead charecters.</p>
                    <Link to='/deadchar' id='allBtn'>Dead Characters</Link>
                </div>
            </div>
        </div>
    )
}