import axios from "axios";
import { useEffect, useState } from "react"
import { Link, useParams } from "react-router-dom";



export function DesignAllCharector(){
    const params = useParams();
    const [products , setProducts] = useState([]);

    useEffect(()=>{
        axios({
            method:'get',
            url:`https://rickandmortyapi.com/api/character`
        })
        .then(response=>{
            setProducts(response.data.results);
        })
    },[]);
    return(
        <div className="container-fluid">
            <div className="d-flex justify-content-between p-1">
                <h2>All Characters</h2>
                <Link className="btn btn-danger h-25 me-4" to='/'>Back</Link>
            </div>
            <div className="d-flex flex-wrap">
                {
                    products.map(product=>
                        <div key={product.id} className="card p-2 m-2" style={{width:'230px'}}>
                            <img src={product.image} className="card-img-top" />
                            <div className="card-header">
                                <strong>Name : </strong>{product.name}                                   
                                <strong> Species : </strong>{product.species}
                            </div>
                            <div className="card-footer">
                                <Link to={'/allchar/details/' + product.id} className="btn btn-primary mt-2 w-100 p-1 m-1">Details</Link>
                            </div>
                        </div>
                    )
                }
            </div>
        </div>
    )
}