import axios from "axios";
import { useEffect, useState } from "react"
import { Link } from "react-router-dom";


export function DesignAliveCharector(){
    const [alives , setAlives] = useState([]);

    useEffect(()=>{
        axios({
           method:'get',
           url : 'https://rickandmortyapi.com/api/character/?name=rick&status=alive' 
        })
        .then(response=>{
            setAlives(response.data.results)
        })
    },[]);
    return(
        <div className="container-fluid">
           <div className="d-flex justify-content-between p-1">
                <h2>Alive Characters</h2>
                <Link className="btn btn-danger h-25 me-4" to='/'>Back</Link>
           </div>
           <div className="d-flex flex-wrap">
                {
                    alives.map(alive=>
                        <div key={alive.id} className="card p-1 m-2" style={{width:'230px'}}>
                            <img src={alive.image} height='200' className="card-img-top" />
                            <div className="card-header">
                                <strong>Name: </strong>{alive.name}<br />
                                <strong> Species: </strong>{alive.species}
                            </div>
                            <div className="card-footer">
                                <Link to={'/alivechar/details/' + alive.id} className="btn btn-primary mt-2 w-100 p-1 m-1">Details</Link>
                            </div>
                        </div>
                    )
                }
           </div>
        </div>
    )
}