import React from "react";


export class ClassEventComponent extends React.Component{
    constructor(){
        super();
        this.state={
            Name:'',
            Price:0,
            City:''
        }
        this.handleCityChange = this.handleCityChange.bind(this);
    }
        handleChange(e){
            this.setState({
                Name:e.target.value,
                Price:this.state.Price,
                City: this.state.City
            })
        }
        handlePrice(e){
            this.setState({
                Name:this.state.Name,
                Price:e.target.value,
                City:this.state.City
            })
        
        }
        handleCityChange(e){
            this.setState({
                Name:this.state.Name,
                Price:this.state.Price,
                City:e.target.value
            })
        }
        submitClick(){
            alert(JSON.stringify(this.state));
        }
        
    render(){
        return(
            <div className="container-fluid">
                <div className="row">
                    <div className="col-3">
                        <dl>
                            <dt>User Name</dt>
                            <dd><input type="text" onChange={this.handleChange.bind(this)} placeholder="enter username" className="form-control" /></dd>
                            <dt>Price</dt>
                            <dd><input type="number" onChange={this.handlePrice.bind(this)} placeholder="enter price" className="form-control" /></dd>
                            <dt>City</dt>
                            <dd>
                                <select onChange={this.handleCityChange}>
                                    <option>Select City</option>
                                    <option>Delhi</option>
                                    <option>Hyd</option>
                                </select>
                            </dd>
                        </dl>
                        <button onClick={this.submitClick.bind(this)} className="btn btn-primary w-100">Submit</button>
                    </div>
                    <div className="col-9">
                        <dl>
                            <dt>Name</dt>
                            <dd>{this.state.Name}</dd>
                            <dt>Price</dt>
                            <dd>{this.state.Price}</dd>
                            <dt>City</dt>
                            <dd>{this.state.City}</dd>
                        </dl>
                    </div>
                </div>
            </div>
        )
    }
}