import React from "react";

class LoginComponent extends React.Component{
    componentDidMount(){
        alert("Your Form Requested")
    }
    componentWillUnmount(){
        alert('Your login form will unmount');
    }
    render(){
        return(
            <div>
                <h2>Login Form</h2>
                <dl>
                    <dt>User Name</dt>
                    <dd><input type="text"  /></dd>
                    <dt>Email</dt>
                    <dd><input type="email" /></dd>
                </dl>   
            </div>
        )
    }
}
class RegisterComponent extends React.Component{
    componentDidMount(){
        alert('Register component requested');
    }
    componentWillUnmount(){
        alert('Register form will unmounted')
    }
    render(){
        return(
            <h2>Register Form</h2>
        )
    }
}


export class LifeCycleComponent extends React.Component{
    constructor(){
        super();
        this.state={
            component:''
        }
    }       
    handleLoginClick(){
        this.setState({
            component: <LoginComponent />
        })
    }
    handleRegisterClick(){
        this.setState({
            component: <RegisterComponent />
        })
    }
    render(){
        return(
            <div className="container-fluid">
                <h2>LifeCycle Method</h2>
                <button onClick={this.handleLoginClick.bind(this)} className="btn btn-primary p-2">Login</button>
                <button onClick={this.handleRegisterClick.bind(this)} className="btn btn-primary ms-4 p-2">Register</button>
                <hr />
                <div>
                    {this.state.component}
                </div>
            </div>
        )
    }
}